import React from 'react';
import PropTypes from 'prop-types';
const AlarmSeverity = (props) => {
  const { rowData, columnIndex, column } = props;
  console.log('AlarmSeverity---->>%o', props);

  let level = rowData[column.key];
  let label = '';
  let color = ''
  if (level == 1 || level == '一级告警') {
    color = '#ff6464';
    label = '一级告警';
  } else if (level == 2 || level == '二级告警') {
    color = '#ffa85a';
    label = '二级告警';
  } else if (level == 3 || level == '三级告警') {
    color = '#ffec6f';
    label = '三级告警';
  } else if (level == 4 || level == '四级告警') {
    color = '#8bbcfe';
    label = '四级告警';
  }
  return color == '' ? null : <div style={{background: color, padding: '5px 10px'}}>{label}</div>;
}
AlarmSeverity.propTypes = {
  rowData: PropTypes.object.isRequired
}

export default AlarmSeverity;
