import React from 'react';
import PropTypes from 'prop-types';
import './style.less';

const AlarmStatic = (props) => {
  console.log('AlarmStatic---->>%o', props);
  let {label, levels, exportEvent} = props;
  return <div className="alarmstatic table-bar">
    <span className="static-label">{label}</span>
    <span className="level red_cicle"></span>
    <span className="static-value">({levels.level1 || 0})</span>
    <span className="level orange_cicle"></span>
    <span className="static-value">({levels.level2 || 0})</span>
    <span className="level yellow_cicle"></span>
    <span className="static-value">({levels.level3 || 0})</span>
    <span className="level blue_cicle"></span>
    <span className="static-value">({levels.level4 || 0})</span>
    <div className="tools-content">
      <span className="export" title="数据导出" onClick={() => {exportEvent ? exportEvent() : null}}></span>
    </div>
  </div>
}
AlarmStatic.PropTypes = {
  label: PropTypes.string,
  exportEvent: PropTypes.func,
  levels: PropTypes.shape(
    {
      level1: PropTypes.number,
      level2: PropTypes.number,
      level3: PropTypes.number,
      level4: PropTypes.number
    }
  )
}
AlarmStatic.defaultProps = {
  label: '活动告警',
  exportEvent: null,
  levels: {
    level1: 0,
    level2: 0,
    level3: 0,
    level4: 0
  }
}
export default AlarmStatic;
