import React from 'react';
import {
  generateColumns,
  generateData,
  Column,
  unflatten,
  BaseTable,
  noop,
  callOrReturn,
  normalizeColumns,
  AutoResizer,
  SortOrder,
} from './baseScope';
import { cloneArray, cloneDeep } from './util/utils';
import { ContextMenu, MenuItem, ContextMenuTrigger, connectMenu } from 'react-contextmenu';
import PropTypes from 'prop-types';
import DynamicMenu from './menu';
import AlarmSeverity from './render/AlarmSeverity';
import AlarmStatic from './toolbar/AlarmStatic';
import { exportExcel } from './util/exportExcel';
import './style.less';

const renderStrategy = {
  severity: AlarmSeverity,
};

class SelectionCell extends React.PureComponent {
	_handleChange = (e) => {
	  const { rowData, rowIndex, column } = this.props;
	  const { onChange } = column;

	  onChange({ selected: e.target.checked, rowData, rowIndex });
	};

	render () {
	  const { rowData, column } = this.props;
	  const { selectedRowKeys, rowKey } = column;
	  const checked = selectedRowKeys.includes(rowData[rowKey]);

	  return <input type="checkbox" checked={checked} onChange={this._handleChange} />;
	}
}

class SelectableTable extends React.PureComponent {
  constructor (props) {
    super(props);

    const { selectedRowKeys, defaultSelectedRowKeys, expandedRowKeys, defaultExpandedRowKeys } = props;
    this.state = {
      selectedRowKeys: (selectedRowKeys !== undefined ? selectedRowKeys : defaultSelectedRowKeys) || [],
      expandedRowKeys: (expandedRowKeys !== undefined ? expandedRowKeys : defaultExpandedRowKeys) || [],
      selectRows: [],
    };
    this.selectMap = new Map();
  }
  setSelectedRowKeys (selectedRowKeys) {
    if (this.props.selectedRowKeys !== undefined) {
      return;
    }

    this.setState({
      selectedRowKeys: cloneArray(selectedRowKeys),
    });
  }
  setExpandedRowKeys (expandedRowKeys) {
    // if `expandedRowKeys` is controlled
    if (this.props.expandedRowKeys !== undefined) {
      return;
    }

    this.setState({
      expandedRowKeys: cloneArray(expandedRowKeys),
    });
  }
  removeRowKeysFromState (rowKeys) {
    if (!Array.isArray(rowKeys)) {
      return;
    }

    const state = {};
    if (this.props.selectedRowKeys === undefined && this.state.selectedRowKeys.length > 0) {
      state.selectedRowKeys = this.state.selectedRowKeys.filter((key) => !rowKeys.includes(key));
    }
    if (this.props.expandedRowKeys === undefined && this.state.expandedRowKeys.length > 0) {
      state.expandedRowKeys = this.state.expandedRowKeys.filter((key) => !rowKeys.includes(key));
    }
    if (state.selectedRowKeys || state.expandedRowKeys) {
      this.setState(state);
    }
  }

	_handleSelectChange = ({ selected, rowData, rowIndex }) => {
	  const selectedRowKeys = [...this.state.selectedRowKeys];
	  const key = rowData[this.props.rowKey];
	  if (selected) {
	    if (!selectedRowKeys.includes(key)) {
	      selectedRowKeys.push(key);
	    }
	    if (!this.selectMap.has(key)) {
	      this.selectMap.set(key, rowData);
	    }
	  } else {
	    const index = selectedRowKeys.indexOf(key);
	    if (index > -1) {
	      selectedRowKeys.splice(index, 1);
	    }
	    if (this.selectMap.has(key)) {
	      this.selectMap.delete(key);
	    }
	  }
	  if (this.props.selectedRowKeys === undefined) {
	    this.setState({ selectedRowKeys });
	  }
	  this.props.onRowSelect({ selected, rowData, rowIndex });
	  this.props.onSelectedRowsChange([...this.selectMap.values()]);
	};

	_rowClassName = ({ rowData, rowIndex }) => {
	  const { rowClassName, rowKey } = this.props;
	  const { selectedRowKeys } = this.state;

	  const rowClass = rowClassName ? callOrReturn(rowClassName, { rowData, rowIndex }) : '';
	  const key = rowData[rowKey];

	  return [rowClass, selectedRowKeys.includes(key) && 'row-selected'].filter(Boolean).concat(' ');
	};

	render () {
	  const { columns, children, selectable, selectionColumnProps, ...rest } = this.props;
	  const { selectedRowKeys } = this.state;

	  let _columns = columns || normalizeColumns(children);
	  if (selectable) {
	    const selectionColumn = {
	      width: 40,
	      flexShrink: 0,
	      resizable: false,
	      frozen: Column.FrozenDirection.LEFT,
	      cellRenderer: SelectionCell,
	      ...selectionColumnProps,
	      key: '__selection__',
	      rowKey: this.props.rowKey,
	      selectedRowKeys: selectedRowKeys,
	      onChange: this._handleSelectChange,
	    };
	    _columns = [selectionColumn, ..._columns];
	  }
	  _columns.forEach((col) => {
	    if (renderStrategy[col.key] || renderStrategy[col.dataKey]) {
	      col.cellRenderer = renderStrategy[col.keyt] || renderStrategy[col.dataKey];
	    }
	  });
	  return <BaseTable {...rest} columns={_columns} rowClassName={this._rowClassName} />;
	}
}

SelectableTable.defaultProps = {
  ...BaseTable.defaultProps,
  onRowSelect: noop,
  onSelectedRowsChange: noop,
};

const ConnectedMenu = connectMenu('same_unique_identifier123')(DynamicMenu);
class SelectTable extends React.Component {
  constructor (props) {
    super(props);
  }
	state = {
	  currentRow: null,
	};
	onClickEvent = (type) => {
	  let rm = this;
	  return function (evt) {
	    console.log('evt---->>%o,%o', evt, type);
	    if (rm.props.onClickEvent) {
	      rm.props.onClickEvent(evt, type);
	    }
	  };
	};
	onMouseEvent = (type) => {
	  let rm = this;
	  return function (evt) {
	    if (rm.props.onMouseEvent) {
	      rm.props.onMouseEvent(evt, type);
	    }
	  };
	};
	onContextMenu = (evt) => {
	  this.setState({
	    currentRow: evt.rowData,
	  });
	};
	onMenuClick = (evt, data) => {
	  console.log('onMenuClick---->>%o', data.action);
	  if (this.props.onMenuEvent) {
	    this.props.onMenuEvent(evt, data);
	  }
	};
	rowEventListener = {
	  onClick: this.onClickEvent('onClick'),
	  onDoubleClick: this.onClickEvent('onDoubleClick'),
	  onMouseEnter: this.onMouseEvent('onMouseEnter'),
	  onMouseLeave: this.onMouseEvent('onMouseLeave'),
	  onContextMenu: this.onContextMenu,
	};
	onSelectEvent = (selects) => {
	  console.log('rowselect keys---->>%o', selects);
	  if (this.props.onSelectEvent) {
	    this.props.onSelectEvent(selects);
	  }
	};
	staticAlarmLevels = (data) => {
	  let levels = {
	    level1: 0,
	    level2: 0,
	    level3: 0,
	    level4: 0,
	    total: 0,
	  };
	  for (let i = 0, num = data.length; i < num; i++) {
	    let alarm = data[i];
	    if (alarm.severity == '1' || alarm.severity == '一级告警') {
	      levels.level1++;
	    } else if (alarm.severity == '2' || alarm.severity == '二级告警') {
	      levels.level2++;
	    } else if (alarm.severity == '3' || alarm.severity == '三级告警') {
	      levels.level3++;
	    } else if (alarm.severity == '4' || alarm.severity == '四级告警') {
	      levels.level4++;
	    }
	    levels.total++;
	  }
	  return levels;
	};
	toTree (data) {
	  let map = new Map();
	  const result = [];
	  data.forEach((item, index) => {
	    if (!item.parentIds) {
	      map.set(item.alarmId, item);
	      result.push(item);
	    }
	  });
	  let cache = new Map();
	  data.forEach((item) => {
	    if (item.parentIds) {
	      let fund = item.parentIds.split(',').every((pid) => {
	        if (map.has(pid)) {
	          const parent = map.get(pid);
	          console.log('pid---->>%o', item.alarmId);
	          let childrens = [];
	          if (parent.hasOwnProperty('children')) {
	            childrens = parent['children'] || [];
	          }
	          let isAdd = false;
	          if (!cache.has(item.alarmId + pid)) {
	            isAdd = true;
	            cache.set(item.alarmId + pid, item.alarmId + pid);
	          }
	          console.log('pid childrens---->>%o', childrens);
	          if (isAdd) {
	            if (!parent.hasOwnProperty('children')) {
	              parent['children'] = [];
	            }
	            parent['children'].push(item);
	          }
	          return true;
	        } else {
	          return false;
	        }
	      });
	      if (!fund) {
	        result.push(item);
	      }
	    }
	  });
	  console.log('result---->>%o', result);
	  return result;
	}
	onExport = () => {
	  exportExcel(this.props.columns, this.props.data, '活动告警.xlsx', 'dataKey', 'title');
	};
	render () {
	  let { currentRow } = this.state;
	  let { columns, data, expandKey, menus, staticLabel } = this.props;
	  let levels = this.staticAlarmLevels(data);
	  let datas = this.toTree(cloneDeep(data));
	  return (
	    <div className="table-wrap">
	      <AlarmStatic
	        label={staticLabel}
	        levels={levels}
	        data={data}
	        exportEvent={this.onExport}
	      ></AlarmStatic>
	      <div className="table-content">
	        <AutoResizer>
	          {({ width, height }) => (
	            <ContextMenuTrigger
	              id="same_unique_identifier123"
	              collect={(props) => props}
	              onItemClick={this.onMenuClick}
	            >
	              <SelectableTable
	                fixed
	                rowKey="alarmId"
	                width={width}
	                height={height}
	                columns={columns}
	                data={datas}
	                selectable
	                expandColumnKey={expandKey}
	                rowEventHandlers={this.rowEventListener}
	                onSelectedRowsChange={this.onSelectEvent}
	              />
	            </ContextMenuTrigger>
	          )}
	        </AutoResizer>
	        <ConnectedMenu row={currentRow} menus={menus} />
	      </div>
	    </div>
	  );
	}
}
SelectTable.propTypes = {
  data: PropTypes.array.isRequired,
  columns: PropTypes.array.isRequired,
  expandKey: PropTypes.string.isRequired,
  menus: PropTypes.array,
  onClickEvent: PropTypes.func,
  onMouseEvent: PropTypes.func,
  onSelectEvent: PropTypes.func,
  onMenuEvent: PropTypes.func,
  staticLabel: PropTypes.string,
};
SelectTable.defaultProps = {
  data: [],
  columns: [],
  onClickEvent: null,
  onMouseEvent: null,
  onSelectEvent: null,
  onMenuEvent: null,
  expandKey: 'alarmTitle',
  menus: [],
  staticLabel: '活动告警',
};
export default SelectTable;
