import React from 'react';
import { ContextMenu, MenuItem, ContextMenuTrigger } from 'react-contextmenu';

import BaseTable, {
  Column,
  SortOrder,
  AutoResizer,
  normalizeColumns,
  callOrReturn,
  unflatten,
  TableHeader as BaseTableHeader,
  TableRow as BaseTableRow,
} from 'react-base-table';
import 'react-base-table/styles.css';
function collect (props) {
  return props;
}
const generateColumns = (count = 10, prefix = 'column-', props) =>
  new Array(count).fill(0)
    .map((column, columnIndex) => ({
      ...props,
      key: `${prefix}${columnIndex}`,
      dataKey: `${prefix}${columnIndex}`,
      title: `告警字段 ${columnIndex}`,
      resizable: true,
      width: 150,
      cellRenderer: (rest) => {
        let { cellData, rowData } = rest;
        let id = Math.floor(Math.random() * 100);
        return (
          <div style={{ position: 'relative' }}>
            <div className="well">{cellData}</div>
          </div>
        );
      },
    }));

const generateData = (columns, count = 200, prefix = 'row-') =>
  new Array(count).fill(0)
    .map((row, rowIndex) =>
      columns.reduce(
        (rowData, column, columnIndex) => {
          rowData[column.dataKey] = `告警 ${rowIndex} - 级别 ${columnIndex}`;
          return rowData;
        },
        {
          id: `${prefix}${rowIndex}`,
          parentId: null,
        }
      )
    );

const noop = () => {};
const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
const action = (message) => (args) => console.log(message, args);

const Table = React.forwardRef((props, ref) => <BaseTable ref={ref} width={700} height={400} {...props} />);
Table.Column = Column;
Table.PlaceholderKey = BaseTable.PlaceholderKey;

export {
  React,
  BaseTable,
  Column,
  SortOrder,
  AutoResizer,
  normalizeColumns,
  callOrReturn,
  unflatten,
  BaseTableRow,
  BaseTableHeader,
  generateColumns,
  generateData,
  noop,
  delay,
  action,
  Table,
};
